@import url(https://fonts.googleapis.com/css2?family=Zen+Maru+Gothic:wght@300;400;500;700;900&display=swap);
* {
  box-sizing: border-box;
  font-family: 'Zen Maru Gothic', sans-serif;
}
html,
body {
  height: 100%;
  overflow: hidden;
}
#root {
  height: 100%;
}
.__main {
  max-width: 95%;
  margin: auto;
  background-color: #fff;
  min-height: 80vh;
  width: 100%;
  border-radius: 10px;
  padding: 1.2rem 0px;
  display: flex;
}

.btn {
  background-color: #fff;
  border: none;
  outline: none;
  cursor: pointer;
  width: 230px;
  height: 47px;
  line-height: 47px;
  border-radius: 5px;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.02);
  display: flex;
  padding: 0;
  transition: all 0.3s ease-in-out;
}
.btn:hover {
  background-color: #FF4242;
  color: #fff;
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
}
.btn i {
  flex: 0.2 1;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  font-size: 18px;
  border-right: 1px solid #ebe7fb;
}
.btn span {
  flex: 0.8 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #FF4242;
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #FF4242;
}

@media (max-width: 768px){
  .__main{
    max-width: 100% !important;
  }
}
.main__chatbody,.main__chatbody1 {
  flex-grow: 1;
  background-color: #f4f3f8;
  border-radius: 10px;
  padding: 20px 25px;
  padding-right: 15px;
  display: flex;
}

.main__chatbody1{
  justify-content: center;
  align-items: center;
  display: none !important;
}

.sorryH1{
  text-align: center;
}

@media (max-width: 675px){
  .main__chatbody{
    display: none !important;
  }

  .main__chatbody1{
    display: flex !important;
  }
}
.main__chatlist {
  height: 100%;
  border-right: 1px solid #ebe7fb;
  padding: 0 40px 0 0px;
}
.chatlist__heading {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.btn-nobg {
  background-color: transparent;
  border: none;
  box-shadow: none;
  font-size: 18px;
  cursor: pointer;
  padding: 10px;
  color: #dad9dd;
  outline: none;
}
.search_wrap {
  background-color: #e6e5ea;
  border-radius: 50px;
  margin-top: 15px;
}
.search_wrap input {
  background-color: transparent;
  border: none;
  padding-left: 10px;
  outline: none;
  position: relative;
  top: -3px;
  left: 10px;
  width: 80%;
  padding-right: 0;
}
.search-btn {
  height: 46px;
  border: none;
  background-color: transparent;
  outline: none;
  width: 20%;
  cursor: pointer;
  font-size: 20px;
}
.chatlist__items {
  width: 18rem;
  margin-top:20px;
  overflow: auto;
  height: 35rem !important;
  overflow-x: hidden !important;
}

.chatlist__item {
  display: flex;
  border-bottom: 1px solid #ebe7fb;
  margin-top: 10px;
  cursor: pointer;
  padding: 10px 10px 10px 10px;
  transition: all 0.3s cubic-bezier(0.88, 0.19, 0.37, 1.11);
  -webkit-transform: scale(0);
          transform: scale(0);
  -webkit-animation-name: showIn;
          animation-name: showIn;
  -webkit-animation-duration: 0.2s;
          animation-duration: 0.2s; /* or: Xms */
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-direction: normal;
          animation-direction: normal; /* or: normal */
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out; /* or: ease, ease-in, ease-in-out, linear, cubic-bezier(x1, y1, x2, y2) */
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both; /* or: backwards, both, none */
  -webkit-animation-delay: 0.1s;
          animation-delay: 0.1s; /* or: Xms */
}

.clientList__item{
  width: 90%;
  position: relative;
  left: 5%;
}

@-webkit-keyframes showIn {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes showIn {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
.chatlist__item:first-child {
  margin-top: 0;
}
.avatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;

  margin-right: 20px;
  position: relative;
}
.avatar img {
  max-width: 100%;
  object-fit: cover;
}

.chatlist__item .userMeta p {
  margin: 0;
  padding: 0;
  color: #000;
  font-weight: 600;
  font-size: 14px;
}
.chatlist__item .userMeta span {
  margin: 0;
  padding: 0;
  color: #ceccd3;
  font-weight: 400;
  font-size: 12px;
  display: block;
}
.chatlist__item:hover,
.chatlist__item.active {
  background: #fff;

  border-radius: 10px;
}
.isOnline {
  position: absolute;
  width: 10px;
  height: 10px;
  position: absolute;
  bottom: 0;
  right: 0;
  background-color: #ddd;
  border-radius: 50%;
  border: 2px solid #fff;
}
.isOnline.active {
  background-color: rgb(2, 194, 2);
}
.avatar-img {
  overflow: hidden;
  border-radius: 50%;
  width: 100%;
  height: 100%;
}

.addBtn{
  font-size: 24px;
  color: #FF4242;
  position: relative;
  top: 0px;
  left: 13px;
  -webkit-transform: translateY(0px);
          transform: translateY(0px);
  transition: all .2s linear;
}

.addBtn:hover{
  color: #292929;
  -webkit-transform: translateY(-2px);
          transform: translateY(-2px);
}

.searchIcon{
  font-size: 1.3rem;
  color: #FF4242;
  position: relative;
  top: -1px;
  transition: all .3s linear;

}

.searchIcon:hover{
  color: #292929;
}

.dotCLass{
  position: absolute;
  bottom: 0.3rem;
  color: #FF4242;
  right: 0.5rem;
  font-size: 1.3rem;
  -webkit-transform: translateY(0) rotate(90deg);
          transform: translateY(0) rotate(90deg);
  transition: all 0.2s linear;
  z-index: 200;
}

.dotCLass:hover{
  color: #292929;
  -webkit-transform: translateY(-2px) rotate(90deg);
          transform: translateY(-2px) rotate(90deg);
}

.deleteDiv{
  width: 100px;
  height: 50px;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 0px 10px gray;
  background-color: #fff !important;
  position: absolute; 
  right: 2rem;
  top: 2.2rem;
  z-index: 100;
  transition: all 0.5s linear;
  -webkit-transform: scale(0);
          transform: scale(0);
  -webkit-animation-name: showIn;
          animation-name: showIn;
  -webkit-animation-duration: 0.2s;
          animation-duration: 0.2s; /* or: Xms */
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-direction: normal;
          animation-direction: normal; /* or: normal */
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out; /* or: ease, ease-in, ease-in-out, linear, cubic-bezier(x1, y1, x2, y2) */
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both; /* or: backwards, both, none */
  -webkit-animation-delay: 0.1s;
          animation-delay: 0.1s; /* or: Xms */
}

.deleteDiv:hover{
  -webkit-transform: scale(1);
          transform: scale(1);
}

.deleteDiv span{
  color: #FF4242;
  font-size: 14px;
  font-weight: 600;
  margin-left: 0.5rem;
}

.deleteIcon{
  color: #FF4242;
}

.deleteDiv:hover .deleteIcon{
  color: #FF4242;
  -webkit-transform: scale(1);
          transform: scale(1);
}

.deleteDiv:hover span{
  color: #292929;
  -webkit-transform: scale(1);
          transform: scale(1);
}

.downIcon{
  position: absolute; 
  right: 2.5rem;
  top: 1.3rem;
  color: #FF4242;
  -webkit-transform: translateY(0px) rotate(0deg);
          transform: translateY(0px) rotate(0deg);
  transition: all 0.2s linear;
}

.downIcon:hover{
  -webkit-transform: translateY(-2px) rotate(180deg);
          transform: translateY(-2px) rotate(180deg);
  color: #292929;
}

.rotate{
  transition: all 0.2s linear;
  -webkit-transform: translateY(0px) rotate(180deg);
          transform: translateY(0px) rotate(180deg);
}

.ant-btn-primary {
  background-color: #FF4242 !important;
  outline: none !important;
  border: none !important;
}

.ant-btn-primary:hover{
  background-color: #b43232 !important;
}

@media (max-width: 992px){
  .main__chatlist{
    padding: 0px 30px 0px 0px;
  }
}

.main__chatcontent {
  flex-grow: 1;
  padding: 10px 20px;
  max-width: 80%;
}
.content__header {
  padding-bottom: 15px;
  border-bottom: 1px solid #ebe7fb;
}
.current-chatting-user {
  display: flex;
  align-items: center;
}
.current-chatting-user p {
  margin: 0;
  font-weight: 600;
}
.content__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.settings .btn-nobg {
  color: #000;
}
.content__body {
  height: 34rem;
  overflow: auto;
}
.chat__item {
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  margin-bottom: 15px;
  transition: all 0.3s ease;
  -webkit-transform: scale(0);
          transform: scale(0);
  -webkit-transform-origin: right;
          transform-origin: right;
  -webkit-animation-name: showIn;
          animation-name: showIn;
  -webkit-animation-duration: 0.2s;
          animation-duration: 0.2s; /* or: Xms */
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-direction: normal;
          animation-direction: normal; /* or: normal */
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out; /* or: ease, ease-in, ease-in-out, linear, cubic-bezier(x1, y1, x2, y2) */
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both; /* or: backwards, both, none */
  /* animation-delay: 0.2s;  */
}
@-webkit-keyframes showIn {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@keyframes showIn {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
.chat__item .avatar {
  margin-right: 0px;
  margin-left: 20px;
  background: #fff;
  padding: 1px;
}
.chat__item__content {
  background-color: #FF4242;
  color: #fff;
  padding: 15px;
  border-radius: 10px 10px 0 10px;
  max-width: 50%;
  min-width: 215px;
}
.chat__item__content .chat__meta {
  justify-content: space-between;
  display: flex;
  margin-top: 10px;
}
.chat__item__content .chat__meta span {
  font-size: 12px;
  font-weight: 500;
  color: #fff;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.chat__msg {
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-size: 15px;
}

.chat__item.other {
  flex-direction: row-reverse;
  -webkit-transform-origin: left;
          transform-origin: left;
}
.chat__item.other .chat__item__content {
  background-color: #fff;
  color: #000;
  border-radius: 10px 10px 10px 0;
  max-width: 50%;
}
.chat__item.other .avatar {
  margin-right: 20px;
  margin-left: 0px;
}
.chat__item.other .chat__item__content .chat__meta span {
