}
.content__footer {
  padding-top: 20px;
}
.sendNewMessage {
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  padding: 10px;
  border-radius: 8px;
}
.sendNewMessage button, .addFiles {
  width: 36px;
  height: 36px;
  background-color: #FF4242;
  border: none;
  box-shadow: none;
  outline: none;
  cursor: pointer;
  font-size: 16px;
  color: #fff;
  padding: 0;
  border-radius: 5px;
  line-height: 36px;
  transition: all 0.3s cubic-bezier(0.88, 0.19, 0.37, 1.11);
}

.addFiles{
  display: flex;
  justify-content: center;
  align-items: center;
}

.sendNewMessage span {
  margin-left: 5px;
  margin-top: 0px;
}

.sendNewMessage button:hover, .addFiles:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}
.sendNewMessage button i , .addFiles i {
  display: block;
}
.sendNewMessage input {
  flex-grow: 1;
  padding: 0 15px;
  background-color: transparent;
  border: none;
  outline: none;
}
#sendMsgBtn {
  background-color: #FF4242;
  color: #fff;
}

#fileSelect{
  display: none;
}

.settingIcon{
  color: #FF4242;
  -webkit-transform: translateY(0px) rotate(0deg);
          transform: translateY(0px) rotate(0deg);
  transition: all 0.2s linear;
}

.settingIcon:hover{
  color: #292929;
  -webkit-transform: translateY(-2px) rotate(180deg);
          transform: translateY(-2px) rotate(180deg);
}

.closeIcon{
  -webkit-transform: translateY(0px) rotate(0deg);
          transform: translateY(0px) rotate(0deg);
}

.closeIcon:hover{
  -webkit-transform: translateY(-2px) rotate(0deg);
          transform: translateY(-2px) rotate(0deg);
}
